import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { PortfolioCardService } from './portfolio-card.service'
import { PortfolioCard } from './portfolio-card.model'



@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.css']
})
export class PortfolioComponent implements OnInit {

  cards: PortfolioCard [];

  constructor(private title: Title, private cardService: PortfolioCardService) { 
    this.cards= cardService.getCards();
  }

  ngOnInit(): void {
    this.title.setTitle('Annmarie Adams | Portfolio');
  }

}
